/**
 * ANNOUNCEMENTS JAVASCRIPT HANDLER
 *
 * @package
 * @since 4.0.0
 */

import AjaxRequest from './ajax-request';

class ReportedAbuse {

	constructor() {
		jQuery( document ).on( 'click', '.yith-plugin-fw__action-button--view-action', {handler: this}, this.view );
	}

	view( event ) {
		event.preventDefault();

		const message = jQuery( this ).data( 'message' );
		const email	  = jQuery( this ).data( 'email' );

		let template = wp.template( 'yith-wcmv-modal-reported-abuse' ),
			templateButtons = wp.template(
				'yith-wcmv-modal-reported-abuse-buttons'
			);

		yith.ui.modal( {
			width: 700,
			closeSelector: '.close-reported-data-modal',
			classes: {
				wrap: 'reported-data-modal',
			},
			title: yith_wcmv_reported_abuse.modalTitle,
			content: template( { message: message } ),
			footer: templateButtons( { email: email } )
		} );
	}
}

const reportedAbuse = new ReportedAbuse();
